<template>
  <div class="communityIndex">
    <div class="community">
      <h1>社区</h1>
      <!-- 发布动态图标 -->
      <div class="fixed" @click="jump">
        <i class="iconfont">&#xe659;</i>
        <span>发动态</span>
      </div>
      <!-- 滚动列表 -->
      <div class="wrapper" ref="wrapper">
        <div class="content">
          <div class="left">
            <div :class="index%2==0?'infos type1':'infos type2'"
              v-for="(item, index) in listLeft" :key="index"
              @click="addInfoData(item)"
            >
              <div class="img" :style="{'background-image':`url(${item.content.imgs[0]})`}"></div>
              <div class="text">
                <h2 class="sl">{{item.title}}</h2>
                <div class="user">
                  <div class="avator">
                    <img :src="item.avator" alt="">
                    <h6 class="sl">{{item.userName}}</h6>
                  </div>
                  <div class="icon">
                    <i class="iconfont">&#xe67d;</i>
                    <span>{{item.share}}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="right">
            <div :class="index%2==0?'infos type2':'infos type1'"
              v-for="(item, index) in listRight" :key="index"
              @click="addInfoData(item)"
            >
              <div class="img" :style="{'background-image':`url(${item.content.imgs[0]})`}">
              </div>
              <div class="text">
                <h2 class="sl">{{item.title}}</h2>
                <div class="user">
                  <div class="avator">
                    <img :src="item.avator" alt="">
                    <h6 class="sl">{{item.userName}}</h6>
                  </div>
                  <div class="icon">
                    <i class="iconfont">&#xe67d;</i>
                    <span>{{item.look}}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
import Bs from 'better-scroll'
import { getissueAll } from 'api/api'
import { mapMutations } from 'vuex'
import { handleDataIssue } from 'utils/handleData'
export default {
  data () {
    return {
      listLeft: [],
      listRight: []
    }
  },
  methods: {
    ...mapMutations(['addInfoData']),
    initBs () {
      const wrapper = this.$refs.wrapper
      /* eslint-disable no-new */
      new Bs(wrapper, { click: true })
    },
    handleData (data) {
      data = data.reverse()
      data.length = parseInt(data.length / 2) * 2
      data.map((item, index) => {
        if (index % 2 === 0) {
          this.listLeft.push(item)
        } else {
          this.listRight.push(item)
        }
      })
    },
    getInitData () {
      getissueAll().then((res) => {
        const data = handleDataIssue(res.data, this.baseUrl)
        this.handleData(data)
        this.$nextTick(() => {
          this.initBs()
        })
      })
    },
    jump () {
      this.$router.push('/community/issueInfos')
    }
  },
  mounted () {
    this.getInitData()
  }
}
</script>
<style lang="less" scoped>
@import '~style/index.less';
.communityIndex{
  position: relative;
}
.community{
  position: relative;
  h1{
    font-size: .34rem;
    line-height: .88rem;
    text-align: center;
    color: #222;
  }
  .fixed{
    width: 1.02rem;
    height: 1.02rem;
    border-radius: 50%;
    background: @orange;
    color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    right: .3rem;
    bottom: 3.28rem;
    z-index: 1;
    .iconfont{
      font-size: .18rem;
      line-height: .18rem;
      height: 0.18rem;
    }
    span{
      font-size: .24rem;
      line-height: .42rem;
    }
  }
  .wrapper{
    overflow: hidden;
    position: fixed;
    top: .88rem;
    bottom: 1rem;
    left: 0;
    right: 0;
    .content{
      width: 6.9rem;
      margin: auto;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      align-items: flex-start;
      padding-top: .2rem;
      .left{
        width: 45%;
      }
      .right{
        width: 45%;
      }
      .infos{
        width: 100%;
        border-radius: .2rem;
        box-shadow: 0 0 .13rem .13rem #eeeeee;
        margin-bottom: .26rem;
        overflow: hidden;
        .img{
          height: 3.6rem;
          background: lightblue;
          background-size: cover;
          background-position: center center;
        }
        .text{
          width: 100%;
          padding-bottom: .2rem;
          h2{
            width: 100%;
            padding: 0 .2rem;
            margin: .1rem 0;
            box-sizing: border-box;
            font-size: .28rem;
          }
          .user{
            display: flex;
            justify-content: space-between;
            align-items: center;
            .avator{
              display: flex;
              align-items: center;
              img{
                width: .9rem;
                height: .9rem;
                border-radius: 50%;
                margin: 0 .1rem;
              }
              h6{
                font-size: .16rem;
                max-width: 1.3rem;
              }
            }
            .icon{
              font-size: .16rem;
              .iconfont{
                font-size: .16rem;
              }
              span{
                margin: 0 .1rem;
              }
            }
          }
        }
      }
      .type2{
        .img{
          height: 2.6rem;
        }
      }
    }
  }
}
</style>
